@charset "UTF-8";
@font-face {
  font-family: "wooya";
  src: url("fonts/wooya.eot?cdgmyj");
  src: url("fonts/wooya.eot?cdgmyj#iefix") format("embedded-opentype"), url("fonts/wooya.ttf?cdgmyj") format("truetype"), url("fonts/wooya.woff?cdgmyj") format("woff"), url("fonts/wooya.svg?cdgmyj#wooya") format("svg");
  font-weight: normal;
  font-style: normal; }

/**
 * All of the CSS for your admin-specific functionality should be
 * included in this file.
 */
.wooya-wrapper h2, .wooya-wrapper h3, .wooya-wrapper p {
  color: #293238;
  line-height: 15px;
  letter-spacing: 1px; }

.wooya-wrapper h2 {
  font-size: 25px;
  line-height: 32px;
  text-align: center; }

.wooya-wrapper h3 {
  font-size: 20px;
  line-height: 26px; }

.wooya-wrapper p {
  font-size: 12px; }

.wooya-wrapper h2.wooya-files-title:before,
.wooya-wrapper h2.wooya-settings-title:before {
  font-family: "wooya" !important;
  font-size: 21px;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  position: relative;
  top: -15px;
  margin-right: 5px; }

.wooya-wrapper h2.wooya-files-title:before {
  content: ""; }

.wooya-wrapper h2.wooya-settings-title:before {
  content: ""; }
